@media only screen and (max-width: 1370px) {
  .banner {
    &__promo {
      right: calc( -1 * (100vw - $container-width) / 2);
    }
  }
}

@media only screen and (max-width: 991px) {
  .container {
    max-width: 720px;
  }

  .sidebar {
    width: 25%;

    &::after {
      content: '';
      width: calc(100vw - 75% - (100vw - 720px) / 2 + 30px);
      right: -30px;
    }
  }

  .products-list {
    width: 75%;
    padding-left: 100px;
  }

  .product {
    width: 360px;

    &__img {
      width: 170px;
    }
  }
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 540px;
  }

  .header {
    height: 130px;
    padding-top: 20px;
    padding-bottom: 20px;
    flex-direction: column;
  }

  .slider__images {
    height: 350px;
  }

  .banner {

    &__promo {
      width: 150px;
      height: 250px;
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
    }

    &__link {
      font-size: 12px;

      &::after {
        left: 5px;
      }

      &_blue {
        width: 80px;
        height: 102px;
        padding: 5px 0 0 5px;
      }

      &_pink {
        width: 70px;
        height: 150px;
        padding: 80px 0 0 5px;
      }
    }
  }

  .sidebar {
    width: 25%;

    &::after {
      display: none;
    }
  }

  .categories {

    & li:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  .products-list {
    width: 75%;
    padding-left: 20px;
    flex-direction: column;
  }

  .product {
    width: 380px;

    &__img {
      width: 170px;
    }
  }
}

@media only screen and (max-width: 576px) {
  .container {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }


  .slider__images {
    height: 300px;
  }

  .banner {

    &__link {
      font-size: 12px;

      &_pink {
        padding: 70px 0 0 5px;
      }
    }
  }

  .content {
    display: inline-flex;
  }

  .sidebar {
    padding-left: 20px;
    width: 20%;
  }

  .products-list {
    width: 80%;
    padding-left: 0;
    flex-direction: column;
  }

  .product {
    width: 100%;
  }
}
